import React from "react";

function About() {
  const text = React.useRef(null);
  const button = React.useRef(null);

  function expandText() {
    text.current.classList.toggle("expand-text");
    changeButtonText();
  }

  function changeButtonText() {
    if (button.current.innerText.toLowerCase() == "read more") {
      button.current.innerText = "Show less";
    } else {
      button.current.innerText = "Read more";
    }
  }

  return (
    <section className="about" id="about">
      <div className="container">
        <h1>About me</h1>
        <h2>On my way to master React.js</h2>
        <div className="about-text" ref={text}>
          <p>
            In autumn 2021 I completed <a href="https://www.rtu.lv/en" rel="nofollow" target="_blank">Riga Technical Universitie's</a> course -
            "Modern WEB programming languages", where I improved my knowledge of
            HTML, CSS, Bootstrap, JavaScript, PHP and other skills related to
            WEB programming.
          </p>
          <p>
            I also did internships in frontend development where I worked on <a className="internship-projects" href="http://www.underhookah.com/" rel="nofollow" target="_blank">WordPress website built with Elementor </a>
             and <a className="internship-projects" href="https://en.ridoncompany.com/" rel="nofollow" target="_blank">Wix website</a> , created
            custom coded WordPress theme.
          </p>
          <p>
            A significant part of programming knowledge that I have was gained
            through self-study, by Googling, reading official documentation,
            watching online tutorials (<a  href="https://www.udemy.com/course/the-complete-web-development-bootcamp/" rel="nofollow" target="_blank">Udemy</a>, <a href="https://www.codecademy.com/profiles/method1254049688" rel="nofollow" target="_blank">CodeAcademy</a>, FreeCodeCamp, YouTube
            etc.), building my personal projects and practicing a lot.
          </p>
          <p>
            I have a good fundamental understanding of React.js, including React
            Hooks, state management with Redux, React Router, experience of
            design slicing using Figma, InvisionApp and PhotoShop, creating
            pixel-perfect responsive layouts, using Chrome developer tools,
            basic GIMP and GIT knowledge.
          </p>
          <p>This junior frontend developer portfolio was coded with React.js and designed with FIgma.</p>
          <p>As a general rule I am able to find answers to my questions by Googling and using StackOverflow.</p>
          <p>I have a hosting account and use FTP client on a regular basis.</p>
        </div>
        <button ref={button} className="button" onClick={expandText}>
          <span>Read more</span>
        </button>
      </div>
    </section>
  );
}

export default About;
