function PortfolioItem(props) {
  return (
    <div className="portfolio-item">
      <img src={props.img} alt="junior frontend developer portfolio project" />
      <h2>{props.text}{props.datalink ? <a className="datalink" href={props.datalink}>Data page</a> : null}</h2>
      <a href={props.link} target="_blank" >
        <button className="button">
          <span>Visit site</span>
        </button>
      </a>
    </div>
  );
}

export default PortfolioItem;
