import border2 from "../img/border2.png";
import emailIcon from "../img/email.png";
import thankyouimg from "../img/thank-you.png";
import $ from 'jquery';

function Contact() {

  function showThankyouMessage() {
  
    $( "form" ).on( "submit", function(e) {
   
      var dataString = $(this).serialize();
   
      $.ajax({
        type: "POST",
        url: "https://api.contactform.dev/api/send/823870062juniordevtech_contact_form146",
        data: dataString,
        success: function () {
          $("#contact_form").html("<div id='message' class='success-message'></div>");
          $("#message")
            .html("<h2>Contact Form Submitted!</h2>")
            .append("<p>I will get back to you soon.</p>")
            .hide()
            .fadeIn(1500, function () {
              $("#message").append(
                `<img id='checkmark' src=' ${thankyouimg}' />`
              );
            });
        }
      });
   
      e.preventDefault();
    });
  }

  


  return (
    <section className="contact" id="contact">
      <img className="border" src={border2} alt="section border" />
      <div className="container">
        <h1>Get in touch</h1>
        <form action="https://api.contactform.dev/api/send/823870062juniordevtech_contact_form146" method="POST" id="contact_form">
          <img className="email-icon" src={emailIcon} alt="email icon" />
          <span className="form-intro-text">Let's talk!</span>
          <br />
          <input type="text" placeholder="Name..." name="name" required/>
          <input type="email" placeholder="Email..." name="email" required/>
          <br />
          <textarea
            name="message"
            id=""
            cols="30"
            rows="10"
            placeholder="Message here..."
            required
          ></textarea>
          <button type="submit" className="button" onClick={showThankyouMessage}>
            <span>Send</span>
          </button>
        </form>
      </div>
    </section>
  );
}

export default Contact;
