import PortfolioItem from "./PortfolioItem";
import sparkleheart from "../img/sparkleheart.png";
import recipes from "../img/recipes.png";
import portfoliowp from "../img/portfoliowp.png";
import portfolio2 from "../img/portfolio2.png";
import ekonams from "../img/ekonams.png";
import poetik from "../img/poetik.png";
import pineapple from "../img/pineapple.png";
import weatherapp from "../img/weather-app.png";
import keeper from "../img/keeper.png";
import todolist from "../img/todo-list.png";
import dadjokes from "../img/dad-jokes2.png";
import menuapp from "../img/menu-app.mp4";
import ourworld from "../img/ourworld.mp4";

function Portfolio() {
  return (
    <section className="portfolio" id="portfolio">
      <div className="container">
        <h1>See my junior frontend developer portfolio projects</h1>
        <div className="portfolio-items">
          <PortfolioItem img={recipes} link="http://recipes.juniordev.tech/" text="React.js, Redux, search, React Router" />
          <PortfolioItem img={dadjokes} link="https://lailal88.github.io/dad-jokes/" text="Junior React developer job test, passed" />
          <div className="menu-app">
            <div className="video">
            <video src={menuapp} controls></video>
            <img src={recipes} alt="menu app project" />
            </div>
            <h2>React.js, TailWind CSS, Video, scroll icon</h2>
            <a href="#"><button className='button'><span>Visit site</span></button></a>
          </div>
          <PortfolioItem img={sparkleheart} link="http://sparkleh.juniordev.tech/" text="HTML, CSS, JS, PHP, WordPress, ACF, Contact form 7"/>
          
          <PortfolioItem img={portfoliowp} link="http://portfoliowp.juniordev.tech/" text="WordPress, Elementor, WP forms, Yoast SEO" />
          <PortfolioItem img={portfolio2} link="http://portfolio2.juniordev.tech/" text="HTML, CSS, JS, PHP contact form, responsive design" />
          <PortfolioItem img={ekonams} link="https://lailal88.github.io/eko-nams/" text="HTML, CSS, JS, Video, Google map" />
          <div className="menu-app ourworld">
            <div className="video">
            <video src={ourworld} alt="menu app" controls></video>
            <img src={recipes} alt="our world project" />
            </div>
            <h2>React.js, TailWind CSS, image gallery, React scroll</h2>
            <a href="#"><button className='button'><span>Visit site</span></button></a>
          </div>
          <PortfolioItem img={poetik} link="http://poetik.juniordev.tech/" text="HTML, CSS, CSS animations, PHP, WordPress, Contact form 7" />
          <PortfolioItem img={pineapple} link="https://newslettertask.000webhostapp.com/index.php?page=index" text="PHP, MySQL, form validation" datalink="https://newslettertask.000webhostapp.com/index.php?page=result" />
          <PortfolioItem img={weatherapp} link="https://lailal88.github.io/weather/" text="HTml, CSS, JS Fetch API" />
          <PortfolioItem img={todolist} link="https://lailal88.github.io/reacttodo/" text="React.js, useState, array manipulation" />
          <PortfolioItem img={keeper} link="https://lailal88.github.io/keeper/" text="React.js, useState, mapping data" />
        </div>
      </div>
    </section>
  );
}

export default Portfolio;
